import React, { useState } from 'react';
import './main.scss'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';

import Typography from '@mui/material/Typography';
import AlertDialog from './modals/alert';
import RemoveDialog from './modals/removeItemModal/main';
import FormModal from '../../form/FormModal';
import OwnersDialog from './modals/Owners/main';
import { carsInfoCardsMock } from '../../../server/mock';
import EditDialog from './modals/editModal/main';



export default function CarCard() {
  const [list, updateList] = useState(carsInfoCardsMock);
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [isCharged, isChargedSet] = useState(carsInfoCardsMock.isCharged)
  const renderCarCards = (card, index) => {
    /* debugger */
    const id = card.id;
    const handleRemoveItem = () => {
      updateList(list.filter(item => item.id !== id));
      handleOpen();
    };
    return (
      <Card key={index} className="box">
        <CardMedia component="img" height="40" image={card.image} />
        <CardContent>
          <Typography gutterBottom variant="h6" component="div">
            Owner's name: {card.owner}
          </Typography>
         {/*  <Typography>
            Charged: {isCharged ? 'Ready to cruise 👍🚗' : 'Not yet 🚦'}
          </Typography> */}
          <Typography>
          <ul>
            <li>Brand: {card.brand}</li>
            <li>Model: {card.model}</li>
            <li>Year: {card.year}</li>
            <li>Patent: {card.patent}</li>
            <li>Color: {card.color}</li>
          </ul>
          </Typography>          
        </CardContent>
        <CardActions>
          <EditDialog details={card.services} delivered={card.isCharged}></EditDialog>
          <AlertDialog details={card.services}></AlertDialog>
          {/*  <Button size="small">Edit</Button> */}
          <RemoveDialog removal={handleRemoveItem} list={list.length}></RemoveDialog>
        </CardActions>
      </Card>
    )
  };
  return (
    <>
      <div className="flexOptions">
        <FormModal></FormModal>
        <OwnersDialog></OwnersDialog>
      </div>
      <div className="grid">{list.map(renderCarCards)}</div>
    </>
  );
}


