import './main.scss';
import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';

import DialogTitle from '@mui/material/DialogTitle';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

export default function AlertDialog({ details }) {


  const [services, setServices] = useState(details);

  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    debugger
    console.log(services);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button className="btnform" onClick={handleClickOpen} variant="outlined" Button size="small" color="success"> Edit </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {
            "You are about to update \n important informacion \n are you sure you want to continue?"
          }
        </DialogTitle>
        <Box
          component="form"
          sx={{
            '& .MuiTextField-root': { m: 0.2, width: '45ch' },
          }}
          noValidate
          autoComplete="off"
        >
          <div className="formStyles">
            <FormControl>
              <InputLabel >Brand</InputLabel>
              <Select

                id="brandSelect"
                label="Brand"

              >
                <MenuItem value={1}>Acura</MenuItem>
                <MenuItem value={2}>Chevrolet</MenuItem>
                <MenuItem value={3}>Bentley</MenuItem>
                <MenuItem value={4}>Buick</MenuItem>
                <MenuItem value={5}>Alfa romeo</MenuItem>
                <MenuItem value={6}>Cadillac</MenuItem>
              </Select>

              <TextField

                required
                id="outlined-required"
                label="Year"
              />
              <TextField

                required
                id="outlined-required"
                label="Patent"
              />
              <TextField

                required
                id="outlined-required"
                label="Color"
              />
              <Button type="submit" variant="outlined">Update resgistry</Button>
            </FormControl>
          </div>
          <DialogActions>
            <Button style={{ background: "#ff7600", }} variant="contained" > AGREE </Button>
            <Button onClick={handleClose} autoFocus> CLOSE </Button>
          </DialogActions>
        </Box>
      </Dialog>
    </div>
  );
}