import React, { useState } from 'react';
import Button from '@mui/material/Button';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';

import DialogTitle from '@mui/material/DialogTitle';

export default function RemoveDialog({ removal, list }) {

    const [open, setOpen] = useState(false);
    const [handleList, setList] = useState(false);
    const handleClickOpen = () => {
        setOpen(true);
    }; const handleClose = () => {
        setOpen(false);
    };
    const deleteRegistry = () => {
        setOpen(false);
        removal();
    }
    const lastAlertRegistry = () => {
        setOpen(false);
    }

    return (
        <div>
            <Button onClick={handleClickOpen} variant="outlined" color="error">Delete</Button>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {list > 4 ? "Are you sure you want to delete this registry?" : "There has to be a minimum of four registries"}
                </DialogTitle>
                <DialogActions>

                    <Button style={{
                        background: list > 4 ? "#ff3b3b" : "#ff7600",
                    }} variant="contained"
                        onClick={list > 4 ? deleteRegistry : lastAlertRegistry} autoFocus>
                        {list > 4 ? "AGREE" : "UNDERSTOOD"}
                    </Button>
                    <Button onClick={handleClose} autoFocus
                        style={{
                            display: list > 4 ? "block" : "none",
                        }}>
                        {list > 4 ? "CLOSE" : ""}
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}
