/* eslint-disable no-unused-expressions */
import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
export default function EditDialog({ details, delivered}) {
  const [total, totalSet] = useState(0);
  const [isDelivered, isDeliveredSet] = useState(delivered);
  const [services, setServices] = useState(details);
  const [open, setOpen] = useState(false);
  const [firstSer, firstSet] = useState(services.repairOne);
  const [secondSer, secondSet] = useState(services.repairTwo);
  const [thirdSer, thirdSet] = useState(services.repairThree);
  const [fourthSer, fourthSet] = useState(services.repairFour);
  const [fifthSer, fifthSet] = useState(services.repairFour);

  const handleClickOpen = () => {
    console.log(services);
    setOpen(true);
  };

  const handleClose = () => {
    setServices({
      ...services
    })
    console.log(services);
    setOpen(false);
  };

  const updateFirstService = () => {
    firstSet(!firstSer);
    console.log(firstSer)
    if (firstSer === false) {
      totalSet(total + 90);
    } else {
      totalSet(total - 90)
    }
  }

  const updateSecondService = () => {
    secondSet(!secondSer);
    console.log(secondSer)
    if (secondSer === false) {
      totalSet(total + 50);
    } else {
      totalSet(total - 50)
    }
  }

  const updateThirdService = () => {
    thirdSet(!thirdSer);
    console.log(thirdSer)
    if (thirdSer === false) {
      totalSet(total + 40);
    } else {
      totalSet(total - 40)
    }
  }

  const updateFourthService = () => {
   debugger
    fourthSet(!fourthSer);
    console.log(fourthSer)
    if (fourthSer === false) {
      totalSet(total + 20);
    } else {
      totalSet(total - 20)
    }
  }
  const updateFifthService = () => {
   debugger
    fifthSet(!fifthSer);
    console.log(fifthSer)
    if (fifthSer === false) {

      if (window.confirm('\n\n -So how much is it going to be? \n\n 20 Bucks! \n\n -WHAT? 20 BUCKS FOR JUST A TWIST? \n\n The cost for the twist is Free of charge!, The 20 bucks is for knowing where to twist!!')) {
        window.open('https://www.youtube.com/watch?v=JBx4xQeNbmw', '_blank');
      };
      totalSet(total + 20);
    } else {
      totalSet(total - 20)
    }
  }

  const closeUp = () =>{
    isDeliveredSet(!isDelivered);
    alert('this is owners Total to charge: ' + total)
    handleClose()
  }

  

  return (
    <div>
      <Button Button size="small" variant="outlined" onClick={handleClickOpen}> Services </Button>
      <Dialog
        open={open}
        
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Overview of services"}
        </DialogTitle>
        <DialogContent>
          <FormGroup>
            <FormControlLabel control={<Switch  checked={firstSer} onChange={() => updateFirstService()} />} label="Oil Change (Charged for: 90USD$)" />
            <FormControlLabel control={<Switch  checked={secondSer} onChange={() => updateSecondService()} />} label="Filter Change (Charged for: 50USD$)" />
            <FormControlLabel control={<Switch  checked={thirdSer} onChange={() => updateThirdService()} />} label="Belt Change (Charged for: 40USD$)" />
            <FormControlLabel control={<Switch  checked={fourthSer} onChange={() => updateFourthService()} />} label="Belt Change (Charged for: 40USD$)" />
            <FormControlLabel control={<Switch checked={fifthSer} onChange={() => updateFifthService()} />} label="Just a Screw twist **PLUS**" />
            <DialogTitle>
              Total charged : {total}
            </DialogTitle>
          </FormGroup>
        </DialogContent>
        <DialogActions>
          <Button Button variant="outlined"
            onClick={closeUp}
            autoFocus >
            charge services 🛒
          </Button>
          <Button Button variant="outlined"
            onClick={
              handleClose
            }
            autoFocus >
            Not yet! 👨‍🔧
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}