import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';
export default function OwnersDialog({ removal }) {


    const [open, setOpen] = useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    const deleteRegistry = () => {
        setOpen(false);
        removal();
    }

    return (
        <div>
            <Button className="btnform" onClick={handleClickOpen} variant="outlined" disabled color="success">Check list of Owners</Button>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"Are you sure you want to delete this registry?"}
                </DialogTitle>
                <DialogActions>

                    <Button style={{
                        background: "#ff3b3b",
                    }} variant="contained"
                        onClick={deleteRegistry} autoFocus>
                        AGREE
                    </Button>
                    <Button onClick={handleClose} autoFocus>
                        CLOSE
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}
