import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Axios from "axios";
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';

export default function FormModal({ removal }) {

    const [Brand, setBrand] = useState()
    const [Model, setModel] = useState()
    const [Year, setYear] = useState()
    const [Patent, setPatent] = useState()
    const [Color, setColor] = useState()
    const [cars, setCars] = useState([])

    const addEmployee = (event) => {
        event.preventDefault();
        Axios.post("http://localhost:3001/create", {
            Brand: Brand,
            Model: Model,
            Year: Year,
            Patent: Patent,
            Color: Color,
        }).then(() => {
            console.log('Success');
        });
    };

    const getValues = (event) => {
        event.preventDefault();
        debugger
        console.table(Brand, Model, Year, Patent, Color);
    }
    const [open, setOpen] = useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        debugger
        setOpen(false);
    };
    const deleteRegistry = () => {
        setOpen(false);
        removal();
    }
    const getCarsDetail = () => {
        debugger
        Axios.get("http://localhost:3001/cars", {}).then((response) => {
            setCars(response.data);
            console.log('Success to load new data');

        });
    }

    return (
        <div>
            <Button className="btnform" onClick={handleClickOpen} variant="outlined" color="success"> + Create a new Registry</Button>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"Are you sure you want to generate a new registry?"}
                </DialogTitle>
                <Box
                    component="form"
                    sx={{
                        '& .MuiTextField-root': { m: 0.5, width: '35ch' },
                    }}
                    noValidate
                    autoComplete="off"
                >
                    <div className="formStyles">
                        <FormControl>
                            <InputLabel >Brand</InputLabel>
                            <Select

                                id="brandSelect"
                                label="Brand"
                                onChange={event => setBrand(event.target.value)}
                            >
                                <MenuItem value={1}>Acura</MenuItem>
                                <MenuItem value={2}>Chevrolet</MenuItem>
                                <MenuItem value={3}>Bentley</MenuItem>
                                <MenuItem value={4}>Buick</MenuItem>
                                <MenuItem value={5}>Alfa romeo</MenuItem>
                                <MenuItem value={6}>Cadillac</MenuItem>
                            </Select>
                            < TextField placeholder={
                                Brand === 1 ? "ILX" : "" || Brand === 2 ? "Camaro" : "" || Brand === 3 ? "Mulsanne" : "" || Brand === 4 ? "Cascada" : "" || Brand === 5 ? "Giulia" : ""
                                    || Brand === 6 ? "Escalade" : ""
                            }
                                required
                                id="outlined-required"
                                label="Model"
                                onChange={event => setModel(event.target.value)}
                            />
                            <TextField
                                onChange={event => setYear(event.target.value)}
                                required
                                id="outlined-required"
                                label="Year"
                            />
                            <TextField
                                onChange={event => setPatent(event.target.value)}
                                required
                                id="outlined-required"
                                label="Patent"
                            />
                            <TextField
                                onChange={event => setColor(event.target.value)}
                                required
                                id="outlined-required"
                                label="Color"
                            />
                            <Button type="submit" variant="outlined" onClick={addEmployee}>Add new registry</Button>
                        </FormControl>
                    </div>
                    <DialogActions>
                        <Button style={{ background: "#25632f9e", }} variant="contained" onClick={getCarsDetail} autoFocus> AGREE </Button>
                        <Button onClick={handleClose} autoFocus> CLOSE </Button>
                    </DialogActions>
                </Box>
            </Dialog>
        </div>
    )
}
